@import '../../../../theme.scss';
@import '../../../../components.scss';

.root {
  background-image: url('/techstars-bg.png');
  background-position: top;
}

.container {
  display: flex;
  justify-content: space-between;
  @include spacing(padding-top, 64);
  @include spacing(padding-bottom, 64);
  @media (max-width: $breakpoint-middle) {
    display: block;
  }
}

.col {
  display: flex;
  flex-direction: column;
}

.right.col {
  align-items: center;
  text-align: center;
  justify-content: center;
  .title {
    @extend .font-size-14-noto;
    @include spacing(margin-bottom, 16);
    font-weight: 900;
    @media (max-width: $breakpoint-middle) {
      @include spacing(margin-top, 64);
    }
  }
}

.left.col {
  align-items: left;
  @media (max-width: $breakpoint-middle) {
    align-items: center;
    text-align: center;
  }
  .tag {
    @include spacing(margin-bottom, 24);
  }
  .title {
    @extend .font-size-40;
    font-weight: 700;
    @include spacing(margin-bottom, 64);
    em {
      font-style: normal;
      color: #5bf1a0;
    }
  }
}
