@import './theme.scss';

.tag {
  @extend .font-size-14-noto;
  background: rgba(202, 199, 244, 0.19);
  border-radius: 4px;
  padding-left: 8px;
  padding-right: 8px;
  height: 32px;
  text-align: center;
  line-height: 32px;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.8);
  width: fit-content;
  letter-spacing: 2px;
  @media (max-width: $breakpoint-mobile) {
    border-radius: 2px;
    height: 24px;
    line-height: 24px;
  }
}

.wrap-left {
  padding-left: calc(50% - 500.5px);
  @media (max-width: $breakpoint-middle) {
    padding-left: 30px;
  }
}

.wrap-right {
  padding-right: calc(50% - 500.5px);
  @media (max-width: $breakpoint-middle) {
    padding-right: 30px;
  }
}

.wrap {
  @extend .wrap-right;
  @extend .wrap-left;
  width: 100%;
}
