@import '../../../../theme.scss';
@import '../../../../components.scss';

.root {
  @extend .wrap;
  @include spacing(margin-bottom, 160);
  > div + div {
    @include spacing(margin-top, 24);
    &.grid {
      @include spacing(margin-top, 64);
    }
  }
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  @media (max-width: $breakpoint-mobile) {
    padding-right: 0;
    padding-left: 0;
  }
  .font-size-40 {
    font-weight: 700;
  }
  .font-size-20 {
    opacity: 0.9;
    @media (max-width: $breakpoint-mobile) {
      br {
        display: none;
      }
    }
  }
}

.grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 64px 32px;
  @media (max-width: $breakpoint-middle) {
    width: 656px;
  }
  @media (max-width: $breakpoint-mobile) {
    width: 100%;
    gap: 48px 0;
    justify-content: space-between;
  }
}

.profile {
  position: relative;
  text-align: center;
  width: 312px;
  @media (max-width: $breakpoint-mobile) {
    width: calc(50% - 4px);
    margin-left: 0;
  }
  > .font-size-24-noto {
    @include spacing(margin-bottom, 8);
    font-weight: 700;
  }
  > .font-size-16-noto {
    color: #a6a6a6;
    white-space: pre-wrap;
  }
}

.profileImage {
  position: relative;
  > img {
    width: 100%;
    height: auto;
    background-color: #d1d2d5;
  }
  > div {
    transition: all 0.1s;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    opacity: 0;
    @extend .font-size-40;
    background-color: rgba(0, 0, 0, 0.6);
    a {
      color: rgba(255, 255, 255, 0.7);
      :hover {
        color: white;
      }
    }
    a + a {
      margin-left: 16px;
    }
    display: flex;
    align-items: center;
    justify-content: center;
  }
  @include spacing(margin-bottom, 16);
}
.profileImage:hover > div {
  opacity: 1 !important;
}
