@import '../../../../theme.scss';
@import '../../../../components.scss';

.root {
  height: 100vh;
  position: relative;
}

.hidden {
  position: static;
  visibility: hidden;
}

.backedBy {
  @extend .font-size-14-noto;
  display: flex;
  align-items: center;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.72);
  @media (max-width: $breakpoint-mobile) {
    display: none;
  }
}

.navBar {
  position: fixed;
  top: 0;
  z-index: 10;
  transition: top 0;

  @extend .wrap;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include spacing(padding-top, 24);
  @include spacing(padding-bottom, 24);
  background-color: rgba(0, 0, 0, 0.4);
  @media (max-width: $breakpoint-mobile) {
    justify-content: center;
  }
  img {
    height: 20px;
    width: auto;
    @media (max-width: $breakpoint-middle) {
      height: 16px;
    }
  }
}

.titles {
  @extend .wrap;
  position: absolute;
  top: 68px;
  bottom: 102px;
  @media (max-width: $breakpoint-middle) {
    top: 56px;
  }
  @media (max-width: $breakpoint-mobile) {
    top: 48px;
    bottom: 48px;
  }

  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  h1,
  h2 {
    margin-block-start: auto;
    margin-block-end: auto;
  }
  h1 {
    @extend .font-size-72;
    color: white;
    @include spacing(margin-bottom, 24);
    font-weight: 900;
  }
  h2 {
    @extend .font-size-28;
    color: #9e9e9e;
    font-weight: 400;
  }
}

.scrollMore {
  width: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  @include spacing(bottom, 24);
  div + img {
    @include spacing(margin-top, 16);
  }
  @extend .font-size-16-noto;
  color: rgba(255, 255, 255, 0.72);
}
