@import '../../theme.scss';

.circleIndicator {
  width: 96px;
  height: 96px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid rgba(255, 255, 255, 0.8);
  border-radius: 48px;
  @media (max-width: $breakpoint-mobile) {
    width: 70px;
    height: 70px;
    border-radius: 35px;
  }
  > div {
    width: 100%;
    text-align: center;
  }
  > div:nth-child(1) {
    @extend .font-size-24-noto;
    font-weight: 900;
    color: #ffffff;
  }
  > div:nth-child(2) {
    @extend .font-size-14-noto;
    font-weight: middle;
    color: rgba(255, 255, 255, 0.64);
  }
}
.clock {
  display: flex;
  > div + div {
    margin-left: 8px;
  }
}
