@import '../../theme.scss';
@import '../../components.scss';

html {
  scroll-behavior: smooth;
}

.container {
  width: 100%;
}

.wallpaper {
  position: absolute;
  opacity: 0.5;
  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
  z-index: -1;
}

.contectUs {
  @extend .wrap;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .text {
    color: white;
    font-weight: 700;
    @extend .font-size-24;
  }
  @include spacing(margin-top, 32);
  @include spacing(margin-bottom, 32);
  @media (max-width: $breakpoint-middle) {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    > * + * {
      @include spacing(margin-top, 24);
    }
  }
}

footer {
  @extend .wrap;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: #ffffff;
  opacity: 0.9;
  @extend .font-size-14-noto;
  > div + div {
    margin-left: 32px;
  }
  @include spacing(margin-top, 24);
  @include spacing(margin-bottom, 24);

  @media (max-width: $breakpoint-middle) {
    flex-direction: column;
    justify-content: center;
    text-align: center;
    > div + div {
      margin-left: 0;
    }
  }
}
.footerHr {
  border-top: 2px solid #3f00ff;
  border-bottom: none;
  border-left: none;
  border-right: none;
  width: 100%;
}
