$breakpoint-xs: 380px;
$breakpoint-mobile: 768px;
$breakpoint-middle: 1064px;

.font-size-14-noto {
  font-family: 'Noto Sans';
  font-size: 14px;
  letter-spacing: 0.32px;
  line-height: 18px;
  @media (max-width: $breakpoint-middle) {
    font-size: 14px;
    line-height: 22px;
  }
  @media (max-width: $breakpoint-mobile) {
    font-size: 12px;
    line-height: 16px;
  }
}

.font-size-16-noto {
  font-family: 'Noto Sans';
  font-size: 16px;
  line-height: 22px;
  @media (max-width: $breakpoint-middle) {
    font-size: 16px;
    line-height: 22px;
  }
  @media (max-width: $breakpoint-mobile) {
    font-size: 12px;
    line-height: 16px;
  }
}

.font-size-24-noto {
  font-family: 'Noto Sans';
  font-size: 24px;
  line-height: 32px;
  letter-spacing: 0.32px;
  @media (max-width: $breakpoint-middle) {
    font-size: 24px;
    line-height: 32px;
  }
  @media (max-width: $breakpoint-mobile) {
    font-size: 20px;
    line-height: 24px;
  }
}

.font-size-20 {
  font-size: 20px;
  letter-spacing: 0.32px;
  line-height: 28px;
  @media (max-width: $breakpoint-middle) {
    font-size: 16px;
    line-height: 24px;
  }
  @media (max-width: $breakpoint-mobile) {
    font-size: 16px;
    line-height: 24px;
  }
}

.font-size-24 {
  font-size: 24px;
  line-height: 28px;
  letter-spacing: 0.32px;
  @media (max-width: $breakpoint-middle) {
    font-size: 24px;
    line-height: 28px;
  }
  @media (max-width: $breakpoint-mobile) {
    font-size: 20px;
    line-height: 24px;
  }
}

.font-size-28 {
  font-size: 28px;
  line-height: 32px;
  @media (max-width: $breakpoint-middle) {
    font-size: 24px;
    line-height: 28px;
  }
  @media (max-width: $breakpoint-mobile) {
    font-size: 20px;
    line-height: 28px;
  }
}

.font-size-40 {
  font-size: 40px;
  letter-spacing: 0.32px;
  line-height: 48px;
  @media (max-width: $breakpoint-middle) {
    font-size: 32px;
    line-height: 40px;
  }
  @media (max-width: $breakpoint-mobile) {
    font-size: 24px;
    line-height: 32px;
  }
}

.font-size-72 {
  font-size: 72px;
  line-height: 80px;
  letter-spacing: 0.64px;
  @media (max-width: $breakpoint-middle) {
    font-size: 56px;
    line-height: 64px;
  }
  @media (max-width: $breakpoint-mobile) {
    font-size: 32px;
    line-height: 40px;
  }
}

$spacing-data: (
  'default': (
    8: 8px,
    16: 16px,
    24: 24px,
    32: 32px,
    64: 64px,
    160: 160px,
  ),
  'middle': (
    8: 8px,
    16: 16px,
    24: 24px,
    32: 32px,
    64: 64px,
    160: 160px,
  ),
  'mobile': (
    8: 8px,
    16: 8px,
    24: 16px,
    32: 24px,
    64: 48px,
    160: 120px,
  ),
);

@mixin spacing($property, $value) {
  #{$property}: map-get(map-get($spacing-data, 'default'), $value);
  @media (max-width: $breakpoint-middle) {
    #{$property}: map-get(map-get($spacing-data, 'middle'), $value);
  }
  @media (max-width: $breakpoint-mobile) {
    #{$property}: map-get(map-get($spacing-data, 'mobile'), $value);
  }
}
