@import '../../../../theme.scss';
@import '../../../../components.scss';

.screenshot {
  img[alt='screenshot'] {
    width: 880px;
    height: auto;
    border-radius: 8px;
  }
  position: absolute;
  left: calc(50% + 40px);
  top: 0;
  img[alt='deco'] {
    transform: translate(-50%, 10%);
    position: absolute;
    left: 0;
    bottom: 0;
  }
  @media (max-width: $breakpoint-middle) {
    img[alt='screenshot'] {
      width: 656px;
    }
    position: relative;
    left: 0%;
    img[alt='deco'] {
      transform: translate(-50%, 10%) scale(0.8);
    }
  }
  @media (max-width: $breakpoint-mobile) {
    img[alt='screenshot'] {
      width: 100%;
      border-radius: 4px;
    }
    img[alt='deco'] {
      transform-origin: bottom left;
      transform: translate(0, 30%);
      width: 20%;
      height: auto;
    }
  }
}
.root {
  @extend .wrap;
  width: 100%;
  overflow: hidden;
  position: relative;
  @include spacing(margin-top, 160);
  @include spacing(margin-bottom, 160);
  display: flex;
  height: 550px;
  align-items: center;
  @media (max-width: $breakpoint-middle) {
    flex-direction: column;
    align-items: center;
    height: auto;
  }
}

.info {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  em {
    font-style: normal;
    color: #faac14;
  }
  > div + div {
    @include spacing(margin-top, 24);
  }
  > a {
    @include spacing(margin-top, 64);
    @media (max-width: $breakpoint-middle) {
      @include spacing(margin-bottom, 64);
    }
  }
  .font-size-40 {
    font-weight: 700;
  }
  .font-size-20 {
    opacity: 0.9;
  }
  @media (max-width: $breakpoint-middle) {
    width: 100%;
    align-items: center;
    text-align: center;
  }
  @media (max-width: $breakpoint-xs) {
    br {
      display: none;
    }
  }
}
