@import '../../theme.scss';

.root {
  @extend .font-size-20;
  display: flex;
  text-decoration: none;
  align-items: center;
  font-weight: 700;
  svg {
    width: 40px;
    height: 40px;
    margin-left: 16px;
  }
  @media (max-width: $breakpoint-middle) {
    svg {
      width: 24px;
      height: 24px;
      margin-left: 8px;
    }
  }
}
